<template>
    <div class="ballard-total">
        Соответствие общей оценки зрелости гестационному возрасту:
        {{ neuromuscularMaturitySumm }} + {{ physicalMaturitySumm }} = {{ total }} ({{ conformity }})
    </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    data() {
        return {
        }
    },

    computed: {
        ...mapGetters('ballard', [
            'neuromuscularMaturitySumm',
            'physicalMaturitySumm',
        ]),

        neuromuscularMaturity() {
            return this.$store.state.ballard.neuromuscularMaturity
        },

        total() {
            return this.neuromuscularMaturitySumm + this.physicalMaturitySumm
        },

        assessment() {
            return this.$store.state.ballard.assessment
        },

        conformity() {
            let confirmated = this.assessment.filter(option => option.summ <= this.total)
            return confirmated[confirmated.length - 1].weeks
        },
    },
}
</script>

<style>
.ballard-total {
    font-weight: bold;
    text-align: left;
    text-decoration: underline;
    font-size: 14pt;
}
</style>
