<template>
    <div class="ballard-form">
        <h4>Оценка нейромышечной зрелости новорожденного ребенка</h4>
        <div v-for="attribute in physicalMaturity" :key="attribute.index">
            <v-select
                v-model="attribute.value"
                :label="attribute.caption"
                :items="attribute.options"
                item-text="text"
                item-value="value"
                allow-overflow
                :rules="attribute.rules"
                class="ballard-select"
            >
            </v-select>
        </div>
        <div class="physical-maturity-summ" style="text-align: right; padding-right: 10%;">
            Общая оценка физической зрелости: {{ physicalMaturitySumm }}
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    data() {
        return {
        }
    },

    computed: {
        ...mapGetters('ballard', [
            'physicalMaturitySumm'
        ]),

        physicalMaturity() {
            return this.$store.state.ballard.physicalMaturity
        },

        genitalsMale() {
            return this.physicalMaturity.genitalsMale.value
        },

        genitalsFemale() {
            return this.physicalMaturity.genitalsFemale.value
        },
    },

    methods: {

      test() {
        console.log(this.physicalMaturity.genitalsMale.value)
      },

    },

    watch: {
        genitalsMale(val) {
            if (val === null) { return }
            this.physicalMaturity.genitalsFemale.value = null;
        },

        genitalsFemale(val) {
            if (val === null) { return }
            this.physicalMaturity.genitalsMale.value = null;
        },
    },

}

</script>

